<template>
	<div class="book-shelf">
		<div class="login-header">
			<div @click="routerGo" class="back">
				<i></i>
				<span>返回</span>
			</div>
			
			<h1>我的书架</h1>
			
			<router-link to="/">
				<span class="login-home">
					<i></i>
				</span>
			</router-link>
		</div>
		
		<div class="shelf-content">
			<div v-if="kong" :lengTh="lengTh">
				<div class="expression"></div>
				<i>书架空空如也，快去逛逛找书</i>
			</div>
			
			<div v-else>
				<div class="shelf-top">
					<span>共{{maps.length}}本</span>
					
					<span v-if="edit" @click="edIt()">编辑</span>
					
					<span v-else>
						<span v-if="true" @click="edIt()">取消</span>
						<span class="delete" @click="deleTe">删除</span>
					</span>
				</div>
				
				
				<div class="shelf-fix" v-for="(i,index) in maps" :key="index">
					<img :src="[link + i.cover]" class="auto-img" >
					<div class="shelf-right">
						<span>{{i.title}}</span>
						<p>1小时前更新：{{i.lastChapter}}</p>
						<p>阅读到第1章</p>
					</div>
					<div v-show="edit" class="shelf-detail"></div>
					<input v-if="!edit" type="checkbox" :value="index"  v-model="ckeckVal">
				</div>
			</div>
		</div>	
		<ElmTail></ElmTail>
	</div>
</template>

<script>
	import ElmTail from '../components/ElmTail'
	export default {
		name:"BookShelf",
		data(){
			return {
				edit:true,
				kong:true,
				fl:false,
				ckeckVal:[], 
				book:[],
				link:'http://statics.zhuishushenqi.com',
			}
		},
		methods:{
			routerGo(){
				this.$router.back(-1);
				this.edit = true
			},
			edIt(){
				this.edit = !this.edit
			},
			deleTe(){
				let arr = []
				var len = this.maps.length
				console.log(len)
				for (var i = 0; i < len; i++) {
					if(this.ckeckVal.indexOf(i) >= 0){
						
					}else{
						arr.push(this.maps[i])
					}
					sessionStorage.clear()
				}
				this.book = arr
				this.ckeckVal = []
				
				if(this.maps.length === 0){
					this.kong = true
				}
			}
		},
		
		
		created() {
			this.book = this.dataBook
		},
		
		computed:{
			dataBook(){
				return this.$store.state.addBook
			},
			lengTh(){
				if(this.book.length === 0){
					this.kong = true
				}
				if(this.book.length !== 0){
					this.kong = false
				}
			},
			maps(){
				let result = []
				this.book.map(i =>{
					if(i.title){
						let record = result.find(f => f.title === i.title)
						if(!record){
							result.push(i)
						}
					}
				})
				return result
			}
		},
		
		components:{
			ElmTail,
		},
		
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	
	.book-shelf{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		background: #f4f4f4;
		
	}
	
	.login-header{
		width: 100%;
		height: 2.75rem;
		background-color: #b93321;
		color: #fff;
		display: block;
	
		.back{
			display: inline-block;
			text-align: center;
			height: 100%;
			width: 60px;
			float: left;
			
			i{
				display: inline-block;
				background: url(../../public/img/arror-back.png) no-repeat center;
				background-size: 125%;
				height: 100%;
				width: 0.9375rem;
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				font-size: 12px;
				margin-left: 2px;
				color:#fff;
			}
		}
		
		h1{
			width: 70%;
			display: inline-block;
			text-align: center;
			line-height: 2.75rem;
		}
		
		.login-home{
			background: url(../../public/img/home.png) no-repeat center;
			background-size: 35%;
			height: 100%;
			width: 14%;
			float: right;
			display: inline-block;
		}
		
	}
	
	.shelf-content{
		width: 100%;
		height: 88%;
		overflow: auto;
		
		.expression{
			width:6.25rem;
			height: 6.25rem;
			background: url(../../public/img/ku.png) no-repeat center;
			background-size: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			top: 0;
			margin: 40% auto;
		}
		i{
			color: #999;
			font-size: 0.75rem;
			text-align: center;
			margin-top: 0.9375rem;
			position: absolute;
			margin: 60% auto;
			left: 0;
			right: 0;
			text-align: center;
		}
	}

	.shelf-top{
		height: 3.4375rem;
		line-height: 3.4375rem;
		color: #999;
		font-size: 0.75rem;
		padding: 0 0.9375rem;
		
		span:nth-child(2){
			float: right;
		}
		span:nth-child(3){
			float: right;
		}
		span:nth-child(4){
			float: right;
		}
		.delete{
			color: #b93321;
			margin-left: 1.25rem;
		}
	}
	
	.shelf-fix{
		background-color: #fff;
		width: 100%;
		height: 6.5625rem;
		padding: 0.9375rem;
		border-bottom: 1px solid #ebebeb;
		position: relative;
		
		
		img{
			width: 3.625rem;
			height: 100%;
			float: left;
			margin-right: 0.9375rem;
		}
		.shelf-right{
			height: 4.6875rem;
			float: left;
			line-height: 1.25rem;
			width: 65%;
			
			span{
				font-size: 0.875rem;
				font-weight: 600;
				color: #333;
			}
			
			p{
				color: #999;
				font-size: 0.75rem;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 1;
				overflow: hidden;
			}
			
		}
		.shelf-detail{
			width: 3.125rem;
			height: 3.125rem;
			position: absolute;
			top: 0;
			bottom: 0.625rem;
			right: 0;
			margin: auto 0;
			background: url(../../public/img/detail.png) no-repeat center;
			background-size: 50%;
		}
		
		input{
			width: 20px;
			height: 20px;
			position: absolute;
			top: 0;
			bottom: 0.625rem;
			right: 1rem;
			margin: auto 0;
		}
	}
</style>
